.App {
  text-align: center;
  background-size: 100% 200%;
  background-image: -webkit-gradient(linear, left top, left bottom, color-stop(50%, #EF9A9A), color-stop(50%, #A5D6A7));
  background-image: -webkit-linear-gradient(top, #EF9A9A 50%, #A5D6A7 50%);
  background-image: -o-linear-gradient(top, #EF9A9A 50%, #A5D6A7 50%);
  background-image: linear-gradient(to bottom, #EF9A9A 50%, #A5D6A7 50%);
  color: #EEE;
  -webkit-transition: background-position 1s;
  -o-transition: background-position 1s;
  transition: background-position 1s;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100vh;
}

.App-working {
}

.App-break {
  background-position: 0 -100%;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

.container {
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



